import Vue from "vue";
import VueRouter from "vue-router";

import CreateTask from "../components/CreateTask";
import ListTask from "../components/ListTask";
import ActionTask from "../components/ActionTask";
import LogTask from "../components/LogTask";
import ListInstance from "../components/ListInstance"

Vue.use(VueRouter);

export default new VueRouter({
    mode: "history",
    routes: [
        { path: "/create", component: CreateTask },
        { path: "/", component: ListTask, alias: "/list" },
        { path: "/detail/:id", component: ActionTask },
        { path: "/log/:id/:type", component: LogTask },
        { path: "/listinstance", component: ListInstance },
        { path: "*", redirect: "/list" }
    ]
})