<template>
    <div class="container-fluid">
        <top active-name="listInstance" />

        <table class="table table-striped table-hover">
            <thead class="thead-light">
                <tr>
                    <th>实例类型</th>
                    <th>名字</th>
                    <th>状态</th>
                    <th>创建时间</th>
                    <th>最后更新时间</th>
                    <th>日志</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="i in instances" v-bind:key="i.id">
                    <td>{{ i.type }}</td>
                    <td>{{ i.name }}</td>
                    <td>{{ i.status }}</td>
                    <td>{{ i.created }}</td>
                    <td>{{ i.updated }}</td>
                    <td>
                        <router-link target="_self" v-bind:to="'/log/' + i.name + '/instance' ">查看</router-link>
                    </td>
                </tr>
            </tbody>
        </table>

        <div class="row">
            <div class="text-right">
                <div class="btn-group">
                    <button v-for="i in pageTotal" v-bind:key="i"
                            class="btn btn-secpmdary"
                            v-bind:class="{ 'btn-primary': i == currentPage }"
                            v-on:click="setCurrentPage(i)">
                        {{ i }}
                    </button>
                </div>
            </div>
        </div>

        <bottom />
    </div>
</template>

<script>

    export default {
        data: function () {
            return {
            }
        },
        computed: {
            currentPage: function () {
                return this.$store.state.currentPageInst;
            },
            pageTotal: function () {
                return this.$store.state.pageTotalInst;
            },
            instances: function () {
                return this.$store.state.instances;
            }
        },
        methods: {
            setCurrentPage(page) {
                this.$store.commit("setCurrentPagInst", page);
                this.$store.dispatch("ListInstanceAction");
            }
        },
        created() {
            this.$store.dispatch("ListInstanceAction");
        }
    }
</script>