import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";

Vue.use(Vuex);

//const baseUrl = "http://localhost:8080";
const baseUrl = "http://47.254.36.104:3000";
//const listTaskUrl = `${baseUrl}/api/tasks/list`;
//const createTaskUrl = `${baseUrl}/api/tasks/create`;

const taskUrl = `${baseUrl}/schedule/jobs/`;
const listTaskUrl = taskUrl;
const createTaskUrl = taskUrl;
const deleteTaskUrl = taskUrl;
const taskHistoryUrl = `${baseUrl}/schedule/histories`;
const listInstanceUrl = `${baseUrl}/schedule/jobs/get_schedule_instance`;
const instanceLogsUrl = `${baseUrl}/schedule/jobs/get_instance_log`;

export default new Vuex.Store({
    strict: true,
    state: {
        //创建任务控制相关
        lastCreatedTaskId: -1,
        lastCreatedError: "",

        pageSize: 10,

        //任务列表相关
        currentPage: 1,
        total: 0,
        pageTotal: 0,
        tasks: [],
        histories: [],

        //任务实例列表相关
        instances: [],
        currentPageInst: 1,
        totalInst: 0,
        pageTotalInst: 0,
        lastInstanceLogs: null,
    },

    mutations: {
        addTask(currState, task) {
            currState.tasks.push(task);
        },

        clearTasks(currState) {
            currState.tasks.splice(0, currState.tasks.length);
        },

        clearHistories(currState) {
            currState.histories.splice(0, currState.histories.length);
        },

        clearInstances(currState) {
            currState.instances.splice(0, currState.instances.length);
        },

        setCurrentPage(currState, page) {
            currState.currentPage = page;
        },

        setCurrentPagInst(currState, page) {
            currState.currentPageInst = page;
        },

        syncTaskFromServer(currState, serverTask) {
            //ui内部的任务对象描述格式
            const t = {
                id: serverTask.id,
                name: serverTask.name,
                user: serverTask.command_user,
                desc: serverTask.desc,
                cmd: serverTask.command,
                trigger: serverTask.trigger,
                trigger_args: serverTask.trigger_args,
            };
            currState.tasks.push(t);
        },

        syncInstanceFromServer(currState, serverInstance) {
            const i = {
                id: serverInstance.id,
                type: serverInstance.job_type,
                name: serverInstance.job_name,
                status: serverInstance.job_status,
                created: serverInstance.create_time,
                updated: serverInstance.update_time,
            };
            currState.instances.push(i);
        },

        syncHistoryFromServer(currState, serverHistory) {
            //ui内部的任务历史结构
            const h = {
                id: serverHistory.id,
                created: serverHistory.created,
                taken: serverHistory.time_cost,
                code: serverHistory.exit_code,
                stderr: serverHistory.stderr,
                stdout: serverHistory.stdout,
            };
            currState.histories.push(h);
        },

        syncInstPagesFromServer(currState, page) {
            currState.totalInst = page.total;
            currState.pageTotalInst = page.totalPage;
        },

        syncPagesFromServer(currState, page) {
            currState.total = page.total;
            currState.pageTotal = page.totalPage;
        },

        syncLastCreatedTask(currState, id) {
            currState.lastCreatedTaskId = id
            currState.lastCreatedError = ""
        },

        syncLastCreatedError(currState, msg) {
            currState.lastCreatedTaskId = -1;
            currState.lastCreatedError = msg;
        },

        syncLastInstanceLogs(currState, logs) {
            currState.lastInstanceLogs = logs;
        }
    },
    actions: {
        async listTaskAction(context) {
            const size = context.state.pageSize;
            const top = (await Axios.get(`${listTaskUrl}?page=${context.state.currentPage}&pagesize=${size}`)).data;

            context.commit("clearTasks");

            top.data.data.forEach(
                t => context.commit("syncTaskFromServer", t)
            );
            const total = top.data.total;

            let page_total = (total / size) | 0
            if (page_total * size < total) {
                page_total += 1
            }

            const page = {
                totalPage: page_total,
                total: total,
            }

            context.commit("syncPagesFromServer", page);
        },

        async ListInstanceAction(context) {
            const size = context.state.pageSize;
            const top = (await Axios.get(`${listInstanceUrl}?page=${context.state.currentPageInst}&pagesize=${size}`)).data;

            context.commit("clearInstances");

            top.data.data.forEach(
                t => context.commit("syncInstanceFromServer", t)
            );
            const total = top.data.total;

            let page_total = (total / size) | 0
            if (page_total * size < total) {
                page_total += 1
            }

            const page = {
                totalPage: page_total,
                total: total,
            }

            context.commit("syncInstPagesFromServer", page);
        },

        async CreateTaskAction(context, task) {
            const t = {
                group: task.group,
                name: task.name,
                desc: task.desc,
                command: task.cmd,
                command_user: "hadoop",
                targets: "local",
                bu_name: "bdp",
                owner: "tomsdinary.liu"
            };

            const top = (await Axios.post(createTaskUrl, t)).data;

            //没有出错message是null
            if (top.data.message) {
                context.commit("syncLastCreatedError", top.data.message);
            } else { 
                context.commit("syncLastCreatedTask", top.data.id);
            }
        },

        async DeleteTaskAction(context, task) {
            (await Axios.delete(`${deleteTaskUrl}/${task.id}`)).data;
        },

        async SetTaskTriggerAction(context, request) {
            const setTriggerUrl = `${taskUrl}${request.id}/trigger`;
            const switchTriggerUrl = `${taskUrl}${request.id}/switch`;

            const r = {
                trigger: request.trigger,
                trigger_args: request.trigger_args,
            };

            (await Axios.post(setTriggerUrl, r)).data;
            (await Axios.post(switchTriggerUrl, r)).data;
        },

        async GetTaskHistoryAction(context, taskId) {
            const top = (await Axios.get(`${taskHistoryUrl}/${taskId}`)).data;
            
            context.commit("clearHistories");

            top.data.forEach(
                h => context.commit("syncHistoryFromServer", h)
            );
        },

        async GetInstanceLogs(context, name) {
            //只测试
            name = "tensorflow_1";

            const url = `${instanceLogsUrl}?task_instance_name=${name}`;
            const top = (await Axios.get(url)).data;
            if (top.message) {
                top.data = top.message;
            } else {
                top.data = top.data.data
            }
            context.commit("syncLastInstanceLogs", top.data);
        }
    }
})