<template>
    <div class="container-fluid">
        <top active-name="list" />
        <table class="table table-striped table-hover">
            <thead class="thead-light">
                <tr>
                    <th>任务名称</th>
                    <th>提交用户</th>
                    <th>任务描述</th>
                    <th>提交命令</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="t in tasks" v-bind:key="t.id">
                    <td>{{ t.name }}</td>
                    <td>{{ t.user }}</td>
                    <td>{{ t.desc }}</td>
                    <td>{{ t.cmd }}</td>
                    <td>
                        <router-link class="form-control" v-bind:to="'/detail/' + t.id">详情</router-link>
                    </td>
                </tr>
            </tbody>
        </table>

        <div class="row">
            <div class="text-right">
                <div class="btn-group">
                    <button v-for="i in pageTotal" v-bind:key="i"
                            class="btn btn-secpmdary"
                            v-bind:class="{ 'btn-primary': i == currentPage }"
                            v-on:click="setCurrentPage(i)">
                        {{ i }}
                    </button>
                </div>
            </div>
        </div>

        <bottom />
    </div>
</template>

<script>

    export default {
        data: function () {
            return {
            }
        },
        computed: {
            currentPage: function() {
                return this.$store.state.currentPage;
            },
            pageTotal: function() {
                return this.$store.state.pageTotal;
            },
            tasks: function() {
                return this.$store.state.tasks;
            }
        },
        methods: {
            setCurrentPage(page) {
                this.$store.commit("setCurrentPage", page);
                this.$store.dispatch("listTaskAction");
            }
        },
        created() {
            this.$store.dispatch("listTaskAction");
        }
    }
</script>