<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-2">
                <button class="btn btn-info" @click="back">
                    返回任务详情页
                </button>
            </div>
            <div class="col">
                <h4>日志内容如下</h4>
            </div>
        </div>
        <hr />

        <pre v-if="type !== 'instance'">
{{ log }}
        </pre>
        <pre v-else>
{{ instanceLog }}
        </pre>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                log: "没有可用日志",
                type: null,
            }
        },
        computed: {
            instanceLog() {
                return this.$store.state.lastInstanceLogs;
            }
        },
        methods: {
            back() {
                this.$router.go(-1)
            }
        },
        created() {
            //这里需要通过_self 跳转过来。在新窗口打开的store状态不对
            let id = this.$route.params.id;
            this. type = this.$route.params.type;
            if (this.type == "instance") {
                this.$store.dispatch("GetInstanceLogs", id)
                return
            }

            const histories = this.$store.state.histories;
            const h = histories.find(x => x.id == id)
            if (this.type === "stderr") {
                this.log = h.stderr;
            }
            if (this.type === "stdout") {
                this.log = h.stdout;
            }
            
        }
    }
</script>
