<template>
    <div class="container-fluid">
        <top active-name="create" />

        <div class="row pt-2">

            <div class="form-group col-6">
                <label for="task-name">任务名字</label>
                <input id="task-name" class="form-control" type="text" v-model="$v.taskName.$model" />
                <validation-error v-bind:validation="$v.taskName" />
            </div>

            <div class="form-group col-6">
                <label for="task-desc">任务描述</label>
                <input id="task-desc" class="form-control" type="text" v-model="taskDesc" />
            </div>

            <div class="form-group col-6">
                <label for="task-group">任务分组</label>
                <select id="task-group" class="form-control" v-model="taskGroup" >
                    <option v-for="g in groups" :key="g">
                        {{ g }}
                    </option>
                </select>
                <validation-error v-bind:validation="$v.taskGroup" />
            </div>

            <div class="form-group col-12 ">
                <label>任务命令</label>
                <textarea class="form-control" rows="10" v-model="$v.taskCmd.$model">
             </textarea>
                <validation-error v-bind:validation="$v.taskCmd" />
            </div>
        </div>

        <div v-if="createResult" class="alert alert-success" role="alert">
            创建成功，请移步任务列表，或者点击 清空 重新创建新的任务。
        </div>

        <div v-if="createError" class="alert alert-danger" role="alert">
            {{ lastCreatedError }}
        </div>

        <div class="row  mb-2 ">
            <div class="col">
                <button v-bind:disabled="!!createResult" class="form-control bg-info" v-on:click="createTaskClick">创建</button>
            </div>
            <div class="col">
                <button class="form-control bg-warning" v-on:click="clearInputClick">清空</button>
            </div>
        </div>

        <bottom />
    </div>
</template>

<script>
    import { required } from "vuelidate/lib/validators";
    import ValidationError from "./ValidationError";

    export default {
        components: { ValidationError },
        validations: {
            taskName: {
                required
            },
            taskCmd: {
                required
            },
            taskGroup: {
                required
            }
        },
        data: function () {
            return {
                groups : ["ad"],
                taskName: "",
                taskDesc: "",
                taskCmd: "",
                taskGroup: "",
                //用于标志是否创建成功的状态
                createResult: false,
                //用于创建报错
                createError: false,
            }
        },
        computed: {
            lastCreatedError() {
                return this.$store.state.lastCreatedError
            }
        },
        methods: {
            createTaskClick() {
                if (this.createResult || this.createError) {
                    return
                }

                this.$v.$touch();
                //验证通过才创建
                if (!this.$v.$invalid) { 

                    let t = {
                        name: this.taskName,
                        desc: this.taskDesc,
                        cmd: this.taskCmd,
                        group: this.taskGroup,
                    };

                    this.$store.watch(state => state.lastCreatedTaskId,
                        (nv) => {
                            if (nv > 0) {
                                this.createResult = true;
                            }
                        })

                    this.$store.watch(state => state.lastCreatedError,
                        (nv) => {
                            if (nv != "") { 
                                this.createError = true;
                            }
                        })

                    this.$store.dispatch("CreateTaskAction", t);
                }
            },
            clearInputClick() {
                this.taskName = "";
                this.taskDesc = "";
                this.taskCmd = "";
                this.taskGroup = "";
                this.createResult = false;
                this.createError = false;
            }
        }
    }
</script>